import {useRef, useEffect, memo} from 'react'
import * as d3 from 'd3'




    const getChart = () => {
        const width = 250;
const  height = 250;
const  margin = 40;

// The radius of the pieplot is half the width or half the height (smallest one). I subtract a bit of margin.
const radius = Math.min(width, height) / 2 - margin

// append the svg object to the div called 'my_dataviz'
const svg = d3.create("svg")
.attr("viewBox", [0, 0, width, height])
    .attr("width", width)
    .attr("height", height)
  .append("g")
    .attr("transform", "translate(" + width / 2 + "," + height / 2 + ")");

// create 2 data_set

let color = d3.scaleOrdinal()
  .domain(["S1", "S2"])
  .range(['#f7ff08', '#ff6e08']);
  var data= {s1: 100000, s2: 70000, }

let arcGenerator = d3.arc()
  .innerRadius(0)
  .outerRadius(radius)

    // Compute the position of each group on the pie:
    let pie = d3.pie()
      .value(function(d) {return d.value; })
      .sort(function(a, b) {  return d3.ascending(a.key, b.key);} ) // This make sure that group order remains the same in the pie chart
      let data_ready = pie(Object.entries(data).map(el => ( 
      {key: el[0], value: el[1]})))
      let u = svg.selectAll("path")
      .data(data_ready)
 
    // Build the pie chart: Basically, each part of the pie is a path that we build using the arc function.
    u
      .enter()
      .append('path')
      .merge(u)
      .transition()
      .duration(1000)
      .attr('d', d3.arc()
        .innerRadius(1)
        .outerRadius(radius)
      )
      .attr('fill', function(d){ return(color(d.data.key)) })
      .attr("stroke", "black")
      .style("stroke-width", "2px")
      .style("opacity", 0.8)

      svg
  .selectAll('mySlices')
  .data(data_ready)
  .enter()
  .append('text')
  .text(function(d){ return  d.data.key + ` (${d.data.value})`})
  .attr("transform", function(d) { return "translate(" + arcGenerator.centroid(d) + ")";  })
  .style("text-anchor", "middle")
  .style("font-size", 13)
        return svg.node()
    }


export const StrategyPieChart = memo(({data}) => {
    const ref = useRef();
    const chart = getChart()
   // const chart = getChart(groups, subgroups, 200000)

  useEffect(() =>{
      ref.current.appendChild(chart);
  }, [chart]);

    return (
        <svg ref={ref} id="my_dataviz" style={{height: 250, width: 250}} />
    )
})

