export const ClientCard = (id) => {
    const params = useParams();
    const data = dataSource.filter(element => element.clientIDs.includes(params.id))
    const PNL = data.reduce((acc, el) => acc + el.PNL, 0) / data.length;
    const volume = data.reduce((acc, el) => acc + el.volume, 0)
    return (
        <div className="wraper" style={{width: 1200, margin: '0 auto'}}>
            <div>
                 PNL: {PNL}%
                <p style={{color: '#777'}}>
                 : {volume}  {' '}
                <span style={{color: 'green'}}>({volume + (volume * PNL / 100)})</span>
                </p>
                

                <h2> </h2>
                <Table
                columns={tickColumns}
                    dataSource={data}
                ></Table>
            </div>
        </div>
       
    )
}