const Ticks = () => {
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const [isTickBarChartShowing, setIsTickBarChartShowing] = useState(false);

  const [selectedTicksData, setSelectedTicksData] = useState([]);

  const onSelectChange = (newSelectedRowKeys, selectedrows) => {
    setSelectedRowKeys(newSelectedRowKeys);
    setSelectedTicksData(
      selectedrows.map(({ volume, PNL, tickId, clientID }) => ({
        clientID,
        volume,
        tickId,
        PNL,
        PNLwithVolume: (volume * PNL) / 100,
      }))
    );
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };

  return (
    <div className="wrapper">
      <Table
        rowSelection={{
          ...rowSelection,
        }}
        dataSource={dataSource}
        columns={tickColumns as any}
        bordered
      />
      <div style={{ display: "flex", flexDirection: "column" }}>
        <Space>
          <h2></h2>
          <div style={{ display: "flex", alignItems: "flex-end" }}>
            <Space>
              <Button
                disabled={selectedRowKeys.length < 1}
                onClick={() => setIsTickBarChartShowing(true)}
              >
                 
              </Button>

              <ClientFilter id="clients-barchart" />
            </Space>
          </div>
        </Space>
        <Space>
          <div id="clients-barchart">
            {isTickBarChartShowing && (
              <TickBarChart
                data={selectedTicksData}
                colors={["#eee", "#48e33a"]}
                subgroups={["volume", "PNLwithVolume"]}
                group="tickId"
              />
            )}
          </div>
        </Space>
        <div>
          <h2> </h2>
          <StrategyPieChart />
        </div>
      </div>
    </div>
  );
};

export default Ticks;
