import { useParams } from "react-router";
import { PoolTable } from "../Instruments/PoolTable";
import { NodesTable } from "../Instruments/NodesTable";
import { SpotOptTable } from "../Instruments/SpotOptTable";
import { IndicatorTable } from "../Instruments/IndicatorTable";
import { Collapse } from "antd";
import { dataSource } from "./Ticks";
import React from "react";

const items = [
  {
    key: "1",
    label: "Nodes 1",
    children: <NodesTable />,
  },
  {
    key: "2",
    label: "Pools",
    children: <PoolTable />,
  },
  {
    key: "3",
    label: "Spot Options",
    children: <SpotOptTable />,
  },
  {
    key: "4",
    label: "Indicator",
    children: <IndicatorTable />,
  },
];

export const TickCard = () => {
  const { id } = useParams();
  const tick = dataSource.find((el) => id === el.tickId);

  return (
    <div style={{ width: 1200, margin: "0 auto" }}>
      <h3> {id} </h3>
      <p>PNL: {tick?.PNL}%</p>
      <p> : {tick?.volume}</p>
      <Collapse items={items} defaultActiveKey={["1"]} />
    </div>
  );
};
