import "./App.css";
import Ticks from "./components/Ticks/Ticks";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import { ClientCard } from "./components/Clients/ClientCard";
import { TickCard } from "./components/Ticks/TickCard";
import { Layout } from "antd";

const { Content } = Layout;
function App() {
  return (
    <BrowserRouter>
      <Layout>
        <header
          style={{
            width: 1200,
            margin: "0 auto",
            marginTop: 20,
            marginBottom: 20,
            fontSize: 20,
          }}
        >
          <Link to="/home">Home</Link>
        </header>
        <Content>
          <Routes>
            <Route path="/home" element={<Ticks />} />
            <Route path="/client/:id" element={<ClientCard />} />
            <Route path="/tick/:id" element={<TickCard />} />
          </Routes>
        </Content>
      </Layout>
    </BrowserRouter>
  );
}

export default App;
