import { useEffect, useRef } from "react";
import * as d3 from "d3";

const getChart = (data, subgroups, maxVolume, colors) => {
  const groups = data.map(({ group }) => group);

  let margin = { top: 10, right: 30, bottom: 20, left: 50 };
  const width = 460 - margin.left - margin.right;
  const height = 400 - margin.top - margin.bottom;

  const svg = d3
    .create("svg")
    .attr("viewBox", [0, 0, width, height])

    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
  let x = d3.scaleBand().domain(groups).range([0, width]).padding([0.2]);
  svg
    .append("g")
    .attr("transform", "translate(0," + height + ")")
    .call(d3.axisBottom(x).tickSizeOuter(0));

  // Add Y axis
  var y = d3.scaleLinear().domain([0, maxVolume]).range([height, 0]);
  svg.append("g").call(d3.axisLeft(y));

  // color palette = one color per subgroup
  let color = d3.scaleOrdinal().domain(subgroups).range(colors);

  //stack the data? --> stack per subgroup
  let stackedData = d3.stack().keys(subgroups)(data);

  // Show the bars
  svg
    .append("g")
    .selectAll("g")
    // Enter in the stack data = loop key per key = group per group
    .data(stackedData)
    .enter()
    .append("g")
    .attr("fill", function (d: any) {
      return color(d.key);
    })
    .selectAll("rect")
    // enter a second time = loop subgroup per subgroup to add all rectangles
    .data(function (d) {
      return d;
    })
    .enter()
    .append("rect")
    .attr("x", function (d) {
      return x(d.data.group);
    })
    .attr("y", function (d) {
      return y(d[1]);
    })
    .attr("height", function (d) {
      return y(d[0]) - y(d[1]);
    })
    .attr("width", x.bandwidth());

  return svg.node();
};

//      48 ,

type IProps = {
  data: any;
  subgroups: string[];
  group: string;
  colors: string[];
  maxVolume?: number;
};

export const TickBarChart: React.FC<IProps> = ({
  data,
  subgroups,
  group,
  colors,
  maxVolume: maxVolumeProp,
}) => {
  const ref = useRef() as any;

  const normalizedData = data.map((el) => ({ ...el, group: el[group] }));

  const maxVolume =
    maxVolumeProp || Math.max(...normalizedData.map((o) => o.volume)) + 10000;
  const chart = getChart(normalizedData, subgroups, maxVolume, colors);

  useEffect(() => {
    if (ref?.current) {
      ref?.current.appendChild(chart);
    }
  }, [chart]);

  return <svg ref={ref} id="my_dataviz" style={{ height: 400, width: 500 }} />;
};
