import { Select, Button, Space } from "antd";
import { useState } from "react";
import { createPortal } from "react-dom";
import { TickBarChart } from "../Graphics/TickBarChart";

export const ClientFilter = ({ id }) => {
  const [selectedClientsIds, setSelectedClientsIds] = useState();
  const handleChange = (value, option) => {
    setSelectedClientsIds(option);
  };

  const domNode = document.getElementById(id);

  const [isClientsBarChartShowing, setIsClientsBarChartShowing] =
    useState(false);
  return (
    <Space>
      <Space>
        <Select
          mode="multiple"
          style={{ minWidth: 150 }}
          onChange={handleChange}
          labelInValue
          options={[
            {
              value: "001",
              group: "001",
              tickId: "15.09.2023",
              PNL: 1,
              label: "001",
              clientID: "001",
              PNLwithVolume: 111000,
              volume: 100000,
            },
            {
              value: "002",
              label: "002",
              PNL: 3,
              clientID: "002",
              PNLwithVolume: 111000,
              volume: 100000,
            },
            {
              value: "003",
              clientID: "003",
              PNLwithVolume: 111000,
              volume: 100000,
              PNL: 4,
              label: "003",
            },
            {
              value: "004",
              label: "004",
              clientID: "004",
              PNLwithVolume: 111000,
              volume: 100000,
              PNL: 11,
            },
            {
              value: "005",
              clientID: "005",
              label: "005",
              PNL: 10,
            },
          ]}
        />
        <Button onClick={() => setIsClientsBarChartShowing(true)}>
           
        </Button>
      </Space>

      {isClientsBarChartShowing &&
        createPortal(
          <TickBarChart
            data={selectedClientsIds}
            colors={["#eee", "#48e33a"]}
            subgroups={["volume", "PNLwithVolume"]}
            group="clientID"
          />,
          domNode
        )}
    </Space>
  );
};
